main.App {
  /* Super basic container to fill the page and center contents */
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  max-height: 100vh;
  padding: 2em;

  display: flex;
  flex-flow: column nowrap;
  overflow: hidden;
}

a {
  color: var(--blue-700);
}

